<template lang="">
<div class="_full_router financial-detail-content">
  <custHeader :title="title"></custHeader>
  <section>
    <div>收货人  收货地址</div>
    <div>
        <div class="financial-box-2" @click="financialDetail(index)">
          <div class="img-contain">
              <img :src="img1">
          </div>
          <div class="text-contain">
            <div>商品名称</div>
            <span class="badge">9折促销</span>
            <div class="qx"><span class="qx-text">￥11111/克</span><span class="nhsyl-text">11111元起</span></div>
          </div>
        </div>

    </div>
  </section>
  <section>
      <mt-cell title="配送方式" vlaue="快递 ￥8.00" is-link></mt-cell>
      <mt-cell title="运费险" vlaue="" is-link></mt-cell>
 </section>

  <div class="balance-cart normal-box">
    <div class="t-right " style="margin-right:6px;"> 合计 &nbsp;: &nbsp;<span>11111</span></div>
    <div class="t-right flew-glow-inherit"><button type="button" @click="submitOrder">提交订单</button></div>
  </div>
  <mt-popup v-model="pVisible" position="bottom" class="add-cart-con">
    <div class="add-cart-contain">
      <div><img :src="img1"> 18K狗眼翡翠
      </div>
      <div class="add-info">品质:纯翡翠</div>
      <div class="add-num normal-box">
        <div class="left">购买数量</div>
        <div class="right btn-component"> <button type="button">-</button><span>1</span><button type="button">+</button></div>
      </div>
      <div class="add-btn"><button type="button" >确定</button></div>
    </div>
  </mt-popup>
</div>
</template>
<script>
import custHeader from 'components/custHeader'

import {
  Swipe,
  SwipeItem,
  Popup
} from 'mint-ui';
import cookieUtil from 'js-cookie'
Vue.component(Swipe.name, Swipe);
Vue.component(SwipeItem.name, SwipeItem);
Vue.component(Popup.name, Popup);

export default {


  data() {
    return {
      pVisible: false,
      title: '确认订单'
    }

  },
  components: {
    custHeader
  },
  computed: {
    img1: function() {
      return require('assets/logo.png');
    }
  },
  created: function() {
      var pars={
          limit:global.pageSize,
          offset:"0",
          shopOrderStatus:"1"
      }
      axiosUtil.upShopOrderQry(pars).then(data=>{
          console.log("订单------")
          console.log(data.data)
      })
  },
  filters: {

  },
  methods: {
    addCartShow: function() {
      this.pVisible = true;
    },
    goBack: function() {
      this.$router.go(-1)
  },
  submitOrder:function(){
      var pars={
          goodsCode:"1111",
          goodsName:"01111",
          spec:"规格描述",
          qty:10
      }
     axiosUtil.upShopOrderIns(pars).then(data=>{
          console.log("订单------")
           console.log(data.data)
     })
  }

  }
};
</script>
<style scoped>
._full_router {
  overflow: auto;
}

.text-contain {
  background: #eee;
}

.text-contain>div {
  padding: 0 12px;
}

.pic-contain {
  width: 100vw;
  height: 80vw;
}

.pic-contain .mint-swipe {
  width: 100vw;
  height: 80vw;
}

.price-o {
  color: #aaa;
}

.price-o span {
  font-size: 12px;
  text-decoration: line-through;
}

.price-sales {
  font-size: 20px;
  color: #f40;
}

.price-sales span {
  font-size: 12px;
  border: 1px solid #f40;
  border-radius: 2px;
  padding: 2px;
}

.goods-name {
  margin: 6px 0;
  font-size: 16px;
  font-weight: bold;
}

.text-contain .goods-base-info {
  padding: 12px;
  background: #fff;
}

.text-contain .normal-box {
  margin-top: 12px;
  margin-bottom: 6px;
  text-align: left;
  font-size: 12px;
  color: #ccc;
}

.text-contain .no-pad {
  padding: 0;
}

.no-pad img {
  width: 100%;
}

.big-title {
  height: 30px;
  line-height: 30px;
  font-size: 12px;
  color: #999;
  text-align: center;
}

.fix-buy {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  border-top: 1px solid #999;
}

.fix-buy button {
  width: 50%;
  float: left;
  height: 38px;
  border: none;
  color: #fff;
}

.add-cart {
  background: rgb(255, 129, 0);
}

.add-buy {
  background: #f40;
}

.add-cart-con {
  width: 100%;
}

.add-cart-contain {
  width: 100%;
  height: 50vh;
  position: relative;
}

.add-cart-contain img {
  width: 80px;
  vertical-align: middle;
}

.add-cart-contain .add-info {
  padding: 12px;
}

.add-cart-contain .add-num {
  padding: 12px;
}

.add-num .left {
  text-align: left;
}

.add-num .right {
  text-align: right;
}

.add-cart-contain .add-btn {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.btn-component button {
  border: none;
  background: #eee;
  border-radius: 2px;
  padding: 0px 12px;
  font-size: 28px;
  color: #999;
  outline: none;
}

.btn-component span {
  padding: 0 12px;
}

.add-cart-contain .add-btn button {
  width: 100%;
  height: 38px;
  background: #f40;
  border: none;
  color: #fff;
}

.login-header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
  height: 45px;
  line-height: 45px;
  font-size: 19px;
  background: rgba(0, 0, 0, 0);
  color: #ffffff;
  text-align: center;
}

.top-back {
  max-width: 85px;
  float: left;
  width: 38px;
  height: 38px;
  background: rgba(0, 0, 0, .5);
  border-radius: 38px;
  line-height: 38px;
  margin-left: 12px;
}

.cart-link {
  width: 38px;
  height: 38px;
  background: rgba(0, 0, 0, .5);
  line-height: 38px;
  border-radius: 38px;
  margin-right: 12px;
}

.cart-link a {
  color: #fff;
}

.balance-cart {
  position: absolute;
  bottom: 0px;
  background: #fff;
  border-top: 1px solid #ccc;
  height: 44px;
  line-height: 44px;
}

.balance-cart button {
  height: 44px;
  width: 100px;
  text-align: center;
  line-height: 44px;
  flex-grow: inherit;
  border: none;
  color: #fff;
  background: #f40;
}


.financial-box-2{
    display: flex;
}
.financial-box-2 .img-contain{
    width: 30vw;
}
.financial-box-2 .img-contain img{
    width: 100%;
}

.financial-box-2  .text-contain{
    padding: 12px;
}
</style>



